@import url(https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,400i,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*  */
body,
html {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat';
}

* {
  box-sizing: border-box;
}

a {
  color: #383838;
}

main {
  min-height: 100vh;
  display: grid;
  place-items: center;
  align-content: center;
  padding: 20vh;
}

.edit-icons {
  cursor: pointer;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

/* .heart {
  padding-top: 40px;
  transform: scale(1.5);
} */

button,
.button {
  font-size: 1rem;
  cursor: pointer;
  background-color: #383838;
  color: white;
  min-width: 6rem;
  padding: .6rem;
  display: block;
}

.button {
  background: #fafafa;
  border: 1px solid #383838;
  color: #383838;
  padding: .4rem;
  max-width: 6rem;
  text-align: center;
  margin-top: 1rem;
}
.project {
    min-height: 350px;
    min-width: 350px;
    
    background: #Ffff;
    position: relative;
    margin-bottom: 30px;
    cursor: pointer;
    
}

.innhold {
    margin: 1rem;
    padding-bottom: 1px;
}

.byline {
    line-height: 30px;
    margin-bottom: 30px;
}

.project:hover {
    box-shadow: 0px 0px 30px -5px rgba(61,61,61,0.10);
}

.project h1 {
    font-size: 1.2rem;
    color: #383838;
}


.admin-icons {
    position: absolute;
    right: 1rem;
    bottom: 1rem;
    display: grid;
    grid-auto-flow: column;
    justify-content: end;
    grid-gap: 1rem;
    gap: 1rem;
    z-index: 1;
}

.project img {
    width: 100%;
}

.lesmer {
    color: black;
    text-decoration: none;
}

@media (max-width: 1000px) {
    .innhold {
    margin-left: 40px;
    }
}


/* project details */


.projectsContainer {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
}

main {
  background-color: #FAFAFA;
  padding: 50px;
}

.overskrift {
  font-size: 17px;
  letter-spacing: 2px;
  color: #3FEBAF;
  padding-bottom: 15px;
}

.add {
  position: fixed;
  width: 100vw;
  left: 0;
  top: 10vh;
  height: 5vh;
  background: #fafafa;
  display: grid;
  place-items: center;
  z-index: 1;
}

.my-masonry-grid {
  /* Not needed if autoprefixing */
  /* Not needed if autoprefixing */
  display: flex;
  margin-left: -5px;
  /* gutter size offset */
  width: auto;
}

.my-masonry-grid_column {
  padding-left: 20px;
  /* gutter size */
  background-clip: padding-box;
}


.navn {
    color: #383838;
    padding-top: 60px;
    padding-bottom: 70px;
    z-index: 1;
    text-align: center;
    font-size: 28px;
    font-weight: 100;
    letter-spacing: 10px;
}

.ux-design {
    color: rgb(255, 191, 94);
    /* color: #e9e9e9; */
    text-align: center;
    font-size: 18px;
    padding-bottom: 70px;
}


.mads {
    color: #3FEBAF;
}

.mads2 {
    letter-spacing: 2px;
    color: #3FEBAF;
}

.heartBeat {
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }


.om-meg {
    padding-bottom: 100px;
    text-align: center;
    padding-left: 20rem;
    padding-right: 20rem;
    color: #383838;
    line-height: 25px;
}

h3 {
    font-size: 19px;
}

p {
    font-size: 14px;
}

@media (max-width: 1000px) {
    .om-meg {
        padding-left: 4rem;
        padding-right: 4rem;
    }

    .navn {
        font-size: 20px;
    }

    .ux-design {
        font-size: 15px;
    }
}
main.project-details {
    justify-content: start;
}

.tilbake {
    padding-top: 20px;
}

.container2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
    width: 100%;
    place-items: center;
    
    
}

.container2 .innhold{
    padding: 0;
}
.container2 .innhold h1{
    margin:0;
    padding:0;
}

.container2 .bilde{
    width:100%;
}
.container2 .bilde img{
    width:100%;
}

.navn2 {
    color: #3FEBAF;
}

.description {
    width: 450px;
    color: #383838;
    line-height: 25px;
}

@media (max-width: 1200px) {
    
    .container2 {
        grid-template-columns: 1fr;
    }

    .innhold {
        margin-left: 0;
    }

}




.header-container {
    position: fixed;
    width: 100vw;
    height: 10vh;
    background: #fafafa;
    display: grid;
    z-index: 2;
}

.burger {
    display: none;
    z-index: 2;
    position: absolute;
    cursor: pointer;
}

header {
    display: grid;
    grid-auto-flow: column;
    justify-content: center;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    z-index: 1;

    align-items: center;
    justify-content: space-between;
    padding-left: 60px;
    padding-right: 60px;
}

header a {
    text-decoration: none;
    color: #383838;

}


.menu2 {
    display: grid;
    grid-auto-flow: column;
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

[aria-current='page'],
.active {
    font-weight: bold;
    color: #3FEBAF;
}

@media (max-width: 1000px) {
    header {
        width: 100%;
        background-color: #383838;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
        grid-auto-flow: row;
        align-content: center;
        text-align: center;
        justify-content: center;
        min-height: 100vh;
        -webkit-transition: .4s ease;
        transition: .4s ease;
    }

    .edit-icons {
        margin-top: 20px;
    }

    header a {
        font-size: 2rem;
        color: #fafafa;
    }

    header.visible {
        -webkit-transform: translateY(0);
                transform: translateY(0);

    }

    .menu2 {
        grid-auto-flow: row;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .burger {
        display: block;
        width: 100%;
        margin-top: 20px;
    }

    main {
        padding: 20vh 5vw;
    }

}

.login {
    text-align: center;
}

.login button {
    
}

.login img{
    width:200px;
    margin-bottom:1rem;

}

.profile{
    display:grid;
    place-items:center;
}
input,
textarea,
label {
    font-size: 1rem;
    padding: .5rem;
    display: block;
    margin-bottom: 1rem;
}

textarea {
    min-width: 60vw;
    min-height: 8rem;
}



.edit img {
    width: 5rem;
}

.edit label {
    padding: 0;
}

.edit {
    -webkit-animation: slideIn .6s ease;
            animation: slideIn .6s ease;
}

@-webkit-keyframes slideIn {
    from {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slideIn {
    from {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
